.main {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: space-between;
    height: 100%;
}

.row {
    display: flex;
    justify-content: space-between;
}

.target {
    background: rgba(0,0,255,0.1);
    display: inline-block;
    padding: 20px;
    cursor: pointer;
}
